import React from "react";
import { useContext } from "react";
import { LanguageContext } from "../../multilingualContext/context.js";
import MultiLingualContent from "../MultilingualContent/MultilingualContent.jsx";
import Dropdown from "react-bootstrap/Dropdown";
import { NavLink } from "react-router-dom";

const Header = () => {
  const { language, toggleLanguage } = useContext(LanguageContext);
  let menu_clicked = false;
  const menuClick = () => {
    if (menu_clicked === true) {
      document
        .querySelector(".header-div-elements-active")
        .classList.remove("header-div-elements-active");
      menu_clicked = false;
    } else {
      document
        .querySelector(".header-div-elements")
        .classList.add("header-div-elements-active");
      menu_clicked = true;
    }
  };

  return (
    <header className="header">
      <div className="header-home">
        <NavLink
          to="/"
          className={(navData) => (navData.isActive ? "active" : "none")}
        >
          <MultiLingualContent contentID="header_home"></MultiLingualContent>
        </NavLink>
      </div>
      <div className="header-div-elements">
        <div className="header-element">
          <NavLink
            to="/services"
            className={(navData) => (navData.isActive ? "active" : "none")}
          >
            <MultiLingualContent contentID="header_services"></MultiLingualContent>
          </NavLink>
        </div>
        <div className="header-element">
          <NavLink
            to="/press-on-nails"
            className={(navData) => (navData.isActive ? "active" : "none")}
          >
            <MultiLingualContent contentID="header_press_on"></MultiLingualContent>
          </NavLink>
        </div>
        <div className="header-element">
          <NavLink
            to="/contact"
            className={(navData) => (navData.isActive ? "active" : "none")}
          >
            <MultiLingualContent contentID="header_contact"></MultiLingualContent>
          </NavLink>
        </div>
        <div className="header-element">
          <Dropdown>
            <Dropdown.Toggle
              variant="success"
              id="dropdown-basic"
              size="sm"
              style={{ backgroundColor: "#fff", border: "none" }}
            >
              <img
                src={language === "english" ? "us.png" : "es.png"}
                alt=""
                style={{ height: "20px" }}
              />
            </Dropdown.Toggle>

            <Dropdown.Menu
              style={{
                minWidth: 0,
                minHeight: 0,
              }}
            >
              <Dropdown.Item href="" onClick={() => toggleLanguage("english")}>
                <img
                  src="us.png"
                  alt=""
                  style={{ width: "30px", verticalAlign: "sub" }}
                />
              </Dropdown.Item>
              <Dropdown.Item href="" onClick={() => toggleLanguage("spanish")}>
                <img
                  src="es.png"
                  alt=""
                  style={{ width: "30px", verticalAlign: "sub" }}
                />
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </div>
      </div>
      <div className="header-menu-logo" onClick={() => menuClick()}>
        <img src="hamburger.png" alt="" />
      </div>
    </header>
  );
};
export default Header;
