import { useRef } from "react";
import Slider from "react-slick";
import { HashLink } from "react-router-hash-link";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import MultiLingualContent from "../MultilingualContent/MultilingualContent.jsx";

export default function ServicesSlider() {
  const sliderRef = useRef(null);
  const scrollWithOffset = (el) => {
    const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
    const yOffset = -80;
    window.scrollTo({ top: yCoordinate + yOffset, behavior: "smooth" });
  };
  const sliderSettings = {
    arrows: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    infinite: true,
    responsive: [
      {
        breakpoint: 1200,
        settings: {
          slidesToShow: 2,
          arrows: false,
        },
      },
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 1,
          arrows: false,
        },
      },
    ],
  };

  const hotelCards = [
    {
      imageSrc: "1.png",
      title: "services_left_service_title_text_1",
      href: "/services#natural-nail-care",
    },
    {
      imageSrc: "2.png",
      title: "services_right_service_title_text_1",
      href: "/services#gel-polish",
    },
    {
      imageSrc: "3.png",
      title: "services_left_service_title_text_2",
      href: "/services#gel-x-extensions",
    },
    {
      imageSrc: "4.png",
      title: "services_right_service_title_text_2",
      href: "/services#poly-gel",
    },
    {
      imageSrc: "5.png",
      title: "services_left_service_title_text_3",
      href: "/services#russian-manicure",
    },
    {
      imageSrc: "2.png",
      title: "services_right_service_title_text_3",
      href: "/services#extras",
    },
  ];
  window.onload = () => {
    document.querySelector(".slick-prev").onclick = function () {
      reinitSlick();
    };
    document.querySelector(".slick-next").onclick = function () {
      reinitSlick();
    };
  };

  var reinitSlick = function () {
    sliderRef.current.slickPause();
  };

  return (
    <div className="content">
      <Slider ref={sliderRef} {...sliderSettings}>
        {hotelCards.map((card, index) => (
          <HashLink
            key={index}
            className="card"
            to={card.href}
            scroll={(el) => scrollWithOffset(el)}
          >
            <img src={card.imageSrc} alt={card.title} className="card-image" />
            <div className="text-info">
              <h3>
                <MultiLingualContent
                  contentID={card.title}
                ></MultiLingualContent>
              </h3>
            </div>
          </HashLink>
        ))}
      </Slider>
    </div>
  );
}
