import "../CSS/style.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { React, useState, useEffect } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { LanguageContext } from "../../multilingualContext/context";
// import NotFound from "../NotFound/NotFound.jsx";
import Landing from "../Landing/Landing.jsx";
import Services from "../Services/Services.jsx";
import Contact from "../Contact/Contact";
import ScrollToTop from "../../scrollToTop";
import PressOnNails from "../PressOnNails/PressOnNails";

function App() {
  const [language, setLanguage] = useState("english");

  useEffect(() => {
    try {
      const storedLanguage = JSON.parse(
        window.localStorage.getItem("language")
      );
      setLanguage(storedLanguage || "english");
    } catch (error) {
      setLanguage("english");
    }
  }, []);

  useEffect(() => {
    window.localStorage.setItem("language", language);
  }, [language]);

  function toggleLanguage(new_language) {
    setLanguage((language) => new_language);
  }

  return (
    <>
      <meta
        name="viewport"
        content="width=device-width, initial-scale=1,
        shrink-to-fit=no"
      />
      <LanguageContext.Provider value={{ language, toggleLanguage }}>
        <BrowserRouter>
          <ScrollToTop />
          <Routes>
            {/* <Route path="*" element={<NotFound />} /> */}
            <Route exact path="/" element={<Landing />}></Route>
            <Route exact path="/services" element={<Services />}></Route>
            <Route
              exact
              path="/press-on-nails"
              element={<PressOnNails />}
            ></Route>
            <Route exact path="/contact" element={<Contact />}></Route>
          </Routes>
        </BrowserRouter>
      </LanguageContext.Provider>
    </>
  );
}

export default App;
