import React from "react";
import MainWrapper from "../MainWrapper/MainWrapper";
import MultiLingualContent from "../MultilingualContent/MultilingualContent";
const PressOnNails = () => {
  return (
    <MainWrapper>
      <div className="services-top-container">
        <div className="services-top-wrapper">
          <div className="services-top-title">
            <MultiLingualContent contentID="press_on_top_title"></MultiLingualContent>
          </div>
          <div className="services-top-subtitle">
            <MultiLingualContent contentID="press_on_top_subtitle"></MultiLingualContent>
          </div>
        </div>
      </div>
      <div className="services-top-wrapper">
        <div className="services-left-service-container">
          <div className="services-left-service-title">
            <div className="gallery-title-text">
              <h6 style={{ textAlign: "justify" }}>
                <MultiLingualContent contentID="press_on_text"></MultiLingualContent>
              </h6>
            </div>
          </div>
        </div>
      </div>
    </MainWrapper>
  );
};
export default PressOnNails;
