import React from "react";
import MainWrapper from "../MainWrapper/MainWrapper";
import MultiLingualContent from "../MultilingualContent/MultilingualContent";
import { useContext } from "react";
import { LanguageContext } from "../../multilingualContext/context.js";
import { translations } from "../../multilingualContext/translations.js";
const Contact = () => {
  const { language } = useContext(LanguageContext);

  return (
    <MainWrapper>
      <div className="services-top-container">
        <div className="services-top-wrapper">
          <div className="services-top-title">
            <MultiLingualContent contentID="contact_top_title"></MultiLingualContent>
          </div>
          <div className="services-top-subtitle">
            <MultiLingualContent contentID="contact_top_subtitle"></MultiLingualContent>
          </div>
        </div>
      </div>
      <div className="form-contact">
        <form
          action="https://public.herotofu.com/v1/dc7a2d40-c112-11ed-a016-ebf293c35465"
          method="post"
          acceptCharset="UTF-8"
        >
          <div>
            <input
              className="contact-input-name"
              name="Name"
              id="name"
              type="text"
              placeholder={translations[language]["contact_name_input"]}
              required
            />
          </div>
          <div>
            <input
              className="contact-input-email"
              name="Email"
              id="email"
              type="email"
              placeholder={translations[language]["contact_email_input"]}
              required
            />
          </div>
          <div>
            <textarea
              className="contact-input-message"
              name="Message"
              id="message"
              type="text"
              placeholder={translations[language]["contact_message_input"]}
              required
            />
          </div>
          <div>
            <input
              type="submit"
              value={translations[language]["contact_submit_input"]}
              className="contact-submit"
            />
          </div>
        </form>
        <div className="contact-form-info">
          <div className="contact-form-title">Gabriela Garca</div>
          <div className="contact-form-email">
            Email: ggarciamunizaga@gmail.com
          </div>
          <div className="contact-form-email">Phone number: 0403766609</div>
          <div className="contact-form-email">Whatsapp: +61 403766609</div>
        </div>
      </div>
    </MainWrapper>
  );
};
export default Contact;
